<template>
  <div class="macro-add">
    <simple-app-bar />
    <grid-body>

      <simple-card>

        <div class="md-layout md-alignment-center-space-around">
          
          <div class="md-layout-item md-size-100 ds-macro-add-total-kcal">
              <span class="md-display-1">{{ carb*4 + prot*4 + fat*9 }} kcal</span>
          </div>

          <div class="md-layout-item md-size-30">
            <md-field>
              <label>CARB (g)</label>
              <md-input v-model.number="carb" type="number"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-size-30">
            <md-field>
              <label>PROT (g)</label>
              <md-input v-model.number="prot" type="number"></md-input>
            </md-field>
          </div>
          
          <div class="md-layout-item md-size-30">
            <md-field>
              <label>FAT (g)</label>
              <md-input v-model.number="fat" type="number"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-size-45">
            <md-button class="md-raised md-accent" @click='addMacros({carb,prot,fat});eraseMacros();'>Add</md-button>
          </div>

          <!-- <div class="md-layout-item md-size-45">
            <md-button class="md-raised md-accent">Save</md-button>
          </div> -->

        </div>

      </simple-card>

      <corner-speed-dial v-bind:main="speeddial.main" v-bind:options="speeddial.options" />
      
    </grid-body>
  </div>
</template>

<script>
import SimpleAppBar from '@/components/AppBar/SimpleAppBar';
import GridBody from '@/components/Grid/GridBody';
import SimpleCard from '@/components/Card/SimpleCard';

import CornerSpeedDial from '@/components/SpeedDial/CornerSpeedDial';

import { mapActions } from 'vuex';

export default {
  name: 'MacroAdd',
  components: {
    SimpleAppBar,
    GridBody,
    SimpleCard,
    CornerSpeedDial,
  },
  data: () => ({
    carb: null,
    prot: null,
    fat: null,
    speeddial: {main:'more_vert',options:[{icon:'home',to:'/'}]},
  }),
  methods: {
    eraseMacros: function(e) {
      this.carb = this.prot = this.fat = 0;
    },
    ...mapActions(['addMacros']),
  }
}
</script>

