import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';
import MacroAdd from './views/MacroAdd.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/macro/add',
      name: 'macroadd',
      component: MacroAdd,
    },
    {
      path: '/about',
      name: 'about',
      component: About,
    },
  ]
})
